
import ViewBox from 'vux/src/components/view-box/index.vue'

import MSearch from '@/components/common/MSearch'
import AppFooter from '@/components/common/MFooter'
import MList from '@/components/common/MList'
import MFilter from '@/components/common/MFilter'

export default {
components:{
ViewBox,
MSearch,
AppFooter,
MList,
MFilter
},
data(){
return {
type: '5',
searchText:'',
sortData:[
{
key:'sortby',
value:'id',
options:[
{key:'id',value:'推荐排序'},
{key:'createdon desc',value:'最新上架'},
{key:'createdon',value:'时间最早'}
]
},
{
key:'wtime',
value:'none',
options:[
{key:'none',value:'不限'},
{key:'week',value:'本周末'},
{key:'inweek',value:'一周内'},
{key:'inmoth',value:'一月内'}
]
}
],
condition:null
}
},
methods:{
onMenuClick(){
console.log('onMenuClick')
},
onSearchClick(v){
console.log('onSearchClick',v)

this.searchText = v
},
onCancelClick(){
this.searchText = ''
},
onSortChange(con){
console.log("onSortChange",con)
this.condition = con
}
}
}



